import React, { useState } from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";
import images from "../images";
import Modal from "./modal";
import { PiCaretCircleDoubleRightFill } from "react-icons/pi";

const ServicesTiles = () => {
  const { t } = useTranslation();
  const services = t("services", { returnObjects: true });

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedService, setSelectedService] = useState<{
    title: string | null;
    copy: string | null;
    document: string | null;
    image: string | null;
    imageMobile: string | null;
  }>({
    title: null,
    copy: null,
    document: null,
    image: null,
    imageMobile: null,
  });

  const handleModalOpen = (service: string) => {
    setSelectedService({
      title: t(`services.${service}.title`),
      copy: t(`services.${service}.copy`),
      document: t(`services.${service}.document`),
      image: t(`services.${service}.modalImage`),
      imageMobile: t(`services.${service}.modalImageMobile`),
    });
    setIsModalOpen(true);
  };

  const handleModalClose = () => {
    setIsModalOpen(false);
    setSelectedService({
      title: null,
      copy: null,
      document: null,
      image: null,
      imageMobile: null
    });
  };

  return (
    <div className={styles.wrapper}>
      {Object.keys(services).map((service, index) => {
        const iconKey = t(`services.${service}.icon`);
        const image = images[iconKey];

        return (
          <div key={index} className={styles.tile} onClick={() => handleModalOpen(service)}>
            {image && (
              <div className={styles.iconWrapper}>
                <img
                  src={image.src}
                  alt={
                    image.alt || t(`services.${service}.title`) || "Default alt"
                  }
                  className={styles.icon}
                />
              </div>
            )}
            <div className="min-h-[80px]">
              <h3 className={styles.title}>{t(`services.${service}.title`)}</h3>
              <p className={styles.paragraph}>{t(`services.${service}.intro`)}</p>
            </div>
            <div className="h-6">
              <PiCaretCircleDoubleRightFill className="text-2xl text-light" />
            </div>
          </div>
        );
      })}

      <Modal
        isOpen={isModalOpen}
        onClose={handleModalClose}
        title={selectedService.title || ""}
      >
        <div className="flex flex-col sm:flex-row">
          {selectedService.image && (
            <div className="w-full sm:w-1/2">
              <img
                src={images[selectedService.image]?.src}
                alt={
                  images[selectedService.image]?.alt || "Default modal image"
                }
                className="hidden sm:block w-full mx-auto"
              />
              <img
                src={images[selectedService.imageMobile ?? 'default-image']?.src}
                alt={
                  images[selectedService.imageMobile ?? 'default-image']?.alt || "Default modal image"
                }
                className="block sm:hidden w-full mx-auto"
              />
            </div>
          )}
          <div className="w-full sm:w-1/2 p-8">
            {selectedService.title && <h2 className="text-2xl font-bold text-dark">{selectedService.title}</h2>}

            <p className="my-8 text-dark">{selectedService.copy}</p>

            {selectedService.document && images[selectedService.document] && (
              <a
                href={images[selectedService.document].src}
                target="_blank"
                rel="noopener noreferrer"
                className="bg-secondary text-light px-4 py-2 rounded-md"
              >
                {t("seeDocument")}
              </a>
            )}
          </div>
        </div>
      </Modal>
    </div>
  );
};

export default ServicesTiles;

const styles = {
  wrapper: "grid grid-cols-2 sm:grid-cols-4 gap-4 itemsx-stretch mx-4 max-w-[1400px] mx-auto px-4 sm:px-0",
  tile: "text-center p-4 rounded-lg cursor-pointer text-white bg-secondary shadow-lg flex flex-col justify-center items-center self-baseline",
  title: "font-bold text-lg/tight",
  paragraph: "text-sm/tight",
  iconWrapper: "w-full flex justify-center h-16",
  icon: "h-12",
};
