import React, { useEffect, useState } from 'react';
import { FaRegWindowClose } from "react-icons/fa";


interface ModalProps {
  isOpen: boolean;
  onClose: () => void;
  title: string;
  children: React.ReactNode;
}

const Modal: React.FC<ModalProps> = ({ isOpen, onClose, title, children }) => {
  const [isModalOpen, setIsModalOpen] = useState(isOpen);

  useEffect(() => {
    setIsModalOpen(isOpen);
  }, [isOpen]);
  
  const handleModalClose = () => {
    setIsModalOpen(false);
    onClose();
  };

  if (!isModalOpen) {
    return null;
  }

  return (
    <div className={styles.overlay} onClick={handleModalClose}>
      <div className={styles.modalContent} onClick={(e) => e.stopPropagation()}>
        <button className={styles.closeButton} onClick={onClose}>
          <FaRegWindowClose className='text-4xl' />
        </button>         
        <div>{children}</div>
      </div>
    
    </div>
  );
};

export default Modal;

const styles = {
    overlay: "fixed inset-0 flex items-center justify-center z-50 w-full h-full bg-black bg-opacity-50",
    modalContent: "relative bg-primary rounded shadow-lg w-full max-w-[960px] m-4",
    closeButton: "absolute right-0 z-50 -top-12 text-white cursor-pointer",
}