import React, { useState } from "react";
import { useTranslation } from "gatsby-plugin-react-i18next";

interface CollapsibleProps {
  data: {
    title: string;
    details: { text: string; index: number }[];
  }[];
}

const Collapsible: React.FC<CollapsibleProps> = ({ data }) => {
  const { t } = useTranslation();
  const [isExpanded, setIsExpanded] = useState(false);

  const togglePanel = () => {
    setIsExpanded((prev) => !prev);
  };

  return (
    <div className={styles.wrapper}>
      <button onClick={togglePanel} className={styles.button}>
        {isExpanded
          ? t("general.hide_details")
          : t("general.show_details")}
      </button>
      {isExpanded && (
        <div className={styles.content}>
          {data.map((item, idx) => (
            <div key={idx} className={styles.item}>
              <h3 className={styles.title}>{t(item.title)}</h3>
              {item.details.map((detail, detailIdx) => (
                <p key={detailIdx} className={styles.text}>
                  {t(detail.text)}
                </p>
              ))}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Collapsible;

const styles = {
  wrapper: "flex flex-col relative z-10 max-w-[1024px] mx-auto",
  button:
    "my-8 text-light hover:text-dark bg-dark hover:bg-semiLight px-4 py-2 rounded max-w-[200px] mx-auto font-semibold text-sm focus:outline-none",
  content: "bg-gray-100 p-6 rounded-lg",
  item: "mb-6 last:mb-0",
  title: "text-lg font-bold mb-2",
  text: "text-sm text-dark",
};
