import * as React from "react";
import { graphql, type HeadFC, type PageProps } from "gatsby";
import { useTranslation } from "gatsby-plugin-react-i18next";
import { Company } from "../interfaces";
import data from "../data/data.json";
import Layout from "./layout";
import images from "../images";
import Carousel from "../components/carousel";
import ServicesTiles from "../components/servicesTiles";
import Collapsible from "../components/collapsible";
import Reviews from "../components/reviews";

const info: Company = data[0];
interface MoreAboutMe {
  title: string;
  details: { text: string; index: number }[];
}

const IndexPage: React.FC<PageProps> = () => {
  const { t } = useTranslation();
  const moreAboutMe: MoreAboutMe[] = t("moreAboutMe", { returnObjects: true });

  const carouselImages = [
    { src: images.carousel_1.src, alt: images.carousel_1.alt },
    { src: images.carousel_2.src, alt: images.carousel_2.alt },
    { src: images.carousel_3.src, alt: images.carousel_3.alt },
    { src: images.carousel_4.src, alt: images.carousel_4.alt },
  ];

  const textOverlay = [
    { content: t("carousel.text_1"), positionClass: styles.slide_1 },
    { content: t("carousel.text_2"), positionClass: styles.slide_2 },
    { content: t("carousel.text_3"), positionClass: styles.slide_3 },
    { content: t("carousel.text_4"), positionClass: styles.slide_3 },
  ];
  return (
    <>
      <Layout data={info}>
        
        <section
          id={info.menu[0]}
          // className="flex w-full mx-auto bg-terciary pt-0 sm:pt-24 pb-4 sm:pb-44 px-4 sm:px-0" Ajuste para remover fondo
          className="flex w-full mx-auto pt-0 sm:pt-24 pb-4 sm:pb-44 px-4 sm:px-0"
        >
          <Carousel
            images={carouselImages}
            textOverlay={textOverlay}
            speed={6000}
          />
        </section>

        <section id={info.menu[1]} className="relative top-4 sm:-top-12">
          <ServicesTiles />
        </section>

        <section id={info.menu[2]} className={styles.whoIam}>
          <div className="flex flex-col">
            <h2 className="text-3xl font-bold text-center">
              {t("whoIam.title")}
            </h2>
            <p className="text-center text-xl italic">{t("whoIam.subTitle")}</p>
          </div>
          <div className="flex flex-col">
            <div className="mx-auto w-48 my-8">
              <img className="rounded-full aspect-[1/1] object-cover" src={images.ana_2.src} alt={images.ana_2.alt} />
            </div>
            <div  className="text-md/tight text-center">
              <p>{t("whoIam.copy_1")}</p>
              <p>{t("whoIam.copy_2")}</p>
            </div>
          </div>
          <Collapsible data={moreAboutMe} />
        </section>

        <section  className="relative mt-12">
          <Reviews />
        </section>

      </Layout>
    </>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>{info.companyName}</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;

const styles = {
  slide_1: "right-0",
  slide_2: "right-0",
  slide_3: "right-0",
  whoIam: "bg-terciary/75 text-dark max-w-[1024px] mx-auto my-8 p-4 sm:p-8",
};
